<!-- Ce composant nous permet de nous passer du router qui est utilis par vue-dsfr pour remplacer a par de simples liens <a>  -->
<!-- Dans le cadre de notre MPA, ce router na pas rellement dintret et la dpendance nest de toute faon pas tire -->
<script setup>

  const props = defineProps(['to', 'active'])
  const current = props.to === '/'
      ? window.location.pathname === props.to
      : window.location.pathname.startsWith(props.to);

</script>

<template>
  <a :href="props.to" :aria-current="current || active ? 'page' : undefined">
    <slot></slot>
  </a>
</template>

